@import "../../app/App.scss";

.home-page {
    header {
        nav {
            display: flex;
            flex-wrap: wrap;
            gap: 2rem;

            .input-group {
                flex-grow: 1;
            }
        }
    }

    .groups {
        ul {
            list-style: none;
            padding-left: 0;
            margin: 0;
        }
        .group {
            border-top: solid $color-primary 0.5rem;

            background: $color-bg;

            margin: 2rem 0;
            padding: 0.5rem 0.5rem;
            h2 {
                margin: 0;
            }

            ul {
                list-style: none;
                padding-left: 0;
                margin: 0;

                display: flex;
                flex-wrap: wrap;
                width: 100%;
                gap: 1rem;
            }
        }
        .course {
            max-width: 100%;
            flex-grow: 1;
            flex-basis: 20rem;

            transition: $transition-primary;

            padding: 1.5rem 0rem;
            margin: 0 1.5rem;

            &:hover {
                color: $color-primary-contrast;
                background-color: $color-primary;
                transition: $transition-primary;

                padding: 1.5rem 1.5rem;
                margin: 0 0;

                .assignments {
                    a {
                        transition: $transition-primary;
                        background-color: $color-primary-contrast;
                        color: $color-primary;
                    }
                }
            }

            .assignments {
                display: flex;
                flex-wrap: wrap;
                a {
                    color: unset;
                    text-decoration: unset;

                    background-color: $color-primary;
                    color: $color-primary-contrast;

                    display: block;
                    padding: 0.2rem 0.5rem;
                    margin: 0.5rem 0;
                }
            }

            & > a {
                color: unset;
                text-decoration: unset;

                display: block;

                h3 {
                    margin-top: 0;
                }
            }
        }
    }
}
